import React, { useCallback } from "react";
import { createSearchParams, useLocation, useNavigate } from "react-router-dom";
import styled from "styled-components";
import { course } from "../data/Subject";
import useAxios from "../api/Axios";
// import { useSelector } from "react-redux";
import { theme } from "../data/ThemeColor";
import { useAuth } from "../Context/Auth";
import { toast } from "react-toastify";

const CreateClass = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const auth = useAuth();
  const axios = useAxios();
  // const { id, name } = useSelector((state) => state.user);
  const [classInfo, setClassInfo] = React.useState({
    teacherName: auth.user.name,
    teacherId: auth.user.id,
  });

  React.useEffect(() => {}, [classInfo.year]);

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setClassInfo({ ...classInfo, [name]: value });
  };

  const handleSubmit = async (evt) => {
    evt.preventDefault();
    const url = "/create";
    await axios
      .post(url, classInfo)
      .then((res) => {
        handleSuccess(res.data.message);
        const path = location.pathname.replace("/create", "/view");
        navigate({
          pathname: path,
          search: createSearchParams({
            id: res.data.id,
          }).toString(),
        });
      })
      .catch((err) => {
        handleError(err.message);
      });
  };

  const handleSuccess = (msg) => {
    toast.success(msg, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });
  };

  const handleError = (msg) => {
    toast.error(msg, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });
  };

  return (
    <Section>
      <Container>
        <Form onSubmit={handleSubmit}>
          <FormHeader>Create New Class</FormHeader>
          <SelectGroup>
            <InputGroup>
              <Label>Year</Label>
              <Select
                name="year"
                defaultValue={classInfo.year}
                onChange={(e) => {
                  handleChange(e);
                  if (e.target.value === "0") {
                    document.getElementsByName("semester").values = null;
                    document.getElementsByName("courseName").values = null;
                    delete classInfo["year"];
                    delete classInfo["semester"];
                    delete classInfo["courseName"];
                  }
                }}
                style={{ marginRight: "10px" }}
                required
              >
                <option value="0">Select Year</option>
                <option value="1">1st Year</option>
                <option value="2">2st Year</option>
                <option value="3">3st Year</option>
                <option value="4">4st Year</option>
              </Select>
            </InputGroup>
            <InputGroup>
              <Label>Semester</Label>
              <Select
                name="semester"
                onChange={(e) => {
                  handleChange(e);
                  if (e.target.value === "0") {
                    document.getElementsByName("courseName").values = null;
                    delete classInfo["semester"];
                    delete classInfo["courseName"];
                  }
                }}
                required
              >
                {classInfo.year !== undefined && (
                  <>
                    <option value="0">Select Semester</option>
                    <option value="1">1st Semister</option>
                    <option value="2">2nd Semister</option>
                  </>
                )}
              </Select>
            </InputGroup>
          </SelectGroup>
          <InputGroup>
            <Label>Course</Label>
            <Select
              name="courseName"
              defaultValue={classInfo.courseName}
              onChange={(e) => {
                const val = e.target.value;

                if (val !== "") {
                  course.map((data) => {
                    if (data.fullTitle === val) {
                      setClassInfo({
                        ...classInfo,
                        courseType: data.courseType,
                        courseName: val,
                      });
                    }
                    return null;
                  });
                } else {
                  delete classInfo["courseName"];
                }
              }}
              required
            >
              {classInfo.year !== undefined &&
                classInfo.semester !== undefined && (
                  <option value="">Select Course Title</option>
                )}
              {classInfo.year !== undefined && classInfo.semester !== undefined
                ? course.map((data) => {
                    return data.year === Number(classInfo.year) &&
                      data.semester === Number(classInfo.semester) ? (
                      <option key={data.code} value={data.fullTitle}>
                        {data.fullTitle}
                      </option>
                    ) : null;
                  })
                : null}
            </Select>
          </InputGroup>
          <InputGroup>
            <Label>Session</Label>
            <Input
              name="session"
              type="text"
              defaultValue={classInfo.session}
              onChange={handleChange}
              placeholder="2018-19"
              required
            />
          </InputGroup>
          <InputGroup>
            <Label>Study Center</Label>
            <Select
              name="center"
              defaultValue={classInfo.center}
              onChange={handleChange}
              required
            >
              <option value="">Select Study Center</option>
              <option value="Dhaka RC">Dhaka RC</option>
              <option value="DUET">DUET</option>
            </Select>
          </InputGroup>
          <InputGroup>
            <Label>Term</Label>
            <Input
              name="term"
              type="number"
              defaultValue={classInfo.term}
              onChange={handleChange}
              placeholder="Term"
              required
            />
          </InputGroup>
          <InputGroup>
            <Label>Color</Label>
            <SelectColor classInfo={classInfo} setClassInfo={setClassInfo} />
          </InputGroup>
          <ButtonGroup>
            <Button
              onClick={() => navigate(-1)}
              style={{ background: "rgba(255, 56, 56,1.0)" }}
            >
              Cancel
            </Button>
            <Button style={{ background: "rgba(72, 52, 212,1.0)" }}>
              Create
            </Button>
          </ButtonGroup>
        </Form>
      </Container>
    </Section>
  );
};

function SelectColor({ classInfo, setClassInfo }) {
  const [bgColor, setBgColor] = React.useState(`rgba(22, 160, 133,1.0)`);
  const [colorName, setColorName] = React.useState("GREEN SEA");
  const [isClicked, setIsClicked] = React.useState(false);

  React.useEffect(() => {}, [isClicked]);

  const handleColorChange = useCallback(
    (color, name, id) => {
      setBgColor(color);
      setColorName(name);
      setIsClicked(!isClicked);
      setClassInfo({ ...classInfo, theme: id });
    },
    [classInfo, isClicked, setClassInfo]
  );

  return (
    <ColorContainer onClick={() => setIsClicked(!isClicked)}>
      <DisplayColor barient={bgColor}>{colorName}</DisplayColor>
      {!isClicked ? (
        <div className="loading">Loading...</div>
      ) : (
        <ColorPanel>
          {theme.map((data) => {
            return (
              <ColorItem
                key={data.id}
                barient={data.color}
                onClick={() =>
                  handleColorChange(data.color, data.title, data.id)
                }
              >
                {data.title}
              </ColorItem>
            );
          })}
        </ColorPanel>
      )}
    </ColorContainer>
  );
}

export default CreateClass;

export const Section = styled.section`
  height: 95vh;
  margin-top: 10px;
`;
export const Container = styled.div``;
export const Form = styled.form`
  padding: 1rem;
  width: 550px;
  border: 1px solid rgba(95, 39, 205, 01);
  border-radius: 5px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem 0;

  @media (max-width: 560px) {
    width: 100%;
    border: none;
  }
`;

export const FormHeader = styled.h1`
  @media (min-width: 540px) {
    font-size: 1.3rem;
  }
`;

export const InputGroup = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

export const Label = styled.label`
  font-size: 0.9rem;
  font-weight: 600;
`;
export const Input = styled.input`
  padding: 0.5rem 1rem;
  border: 1px solid rgba(95, 39, 205, 01);


  &:,
  &:focus {
    outline: none;
    border: 1px solid rgba(95, 39, 205, 0.5);
  }
`;

export const Select = styled.select`
  padding: 0.5rem 1rem;
  border: 1px solid rgba(95, 39, 205, 01);

  &:focus {
    outline: none;
    border: 1px solid rgba(95, 39, 205, 0.5);
  }
`;

export const SelectGroup = styled.div`
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  width: 100%;
`;

export const Span = styled.span`
  background: rgba(95, 39, 205, 01);
  color: #fff;
  border: 1px solid rgba(95, 39, 205, 01);
`;

export const ButtonGroup = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-top: 0.5rem;
  width: 100%;
  justify-content: flex-end;

  @media (max-width: 560px) {
    flex-direction: column-reverse;
  }
`;

export const Button = styled.button`
  border: none;
  padding: 0.5rem 1rem;
  font-weight: 600;
  min-width: 115px;
  color: #fff;
  margin: 1rem 0;
  font-size: 0.9rem;
  margin: 0.5rem;
  &:nth-last-child(1) {
    margin-right: 0;
  }

  @media (max-width: 560px) {
    padding: 0.5rem 1rem;
    min-width: 95%;
    margin: 0.5rem 0;
    font-size: 1.1rem;
  }
`;

const ColorContainer = styled.div`
  width: 100%;
  height: 40px;
  border: 1px solid rgba(95, 39, 205, 01);
  padding: 0.1rem 0.5rem;
  position: relative;
  cursor: pointer;
  background: #fff;
`;

const ColorPanel = styled.div`
  width: 100%;
  height: auto;
  padding: 0.3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  background: rgba(250, 250, 250, 1);
  border: 1px solid rgba(3, 3, 3, 0.2);
  margin-bottom: 2rem;
  box-shadow: 1px 1px 5px 5px rgba(3, 3, 3, 0.2);
  border-radius: 3px;
`;

const ColorItem = styled.div`
  width: 98%;
  height: 25px;
  margin-bottom: 5px;
  cursor: pointer;
  padding: 0 1rem;
  color: #fff;
  background: ${(props) => (props.barient ? props.barient : "transparent")};
`;

const DisplayColor = styled.div`
  position: absolute;
  left: 5px;
  right: 5px;
  top: 5px;
  bottom: 5px;
  margin: auto;
  background: ${(props) => (props.barient ? props.barient : "#fff")};
  color: ${(props) => (props.barient !== "transparent" ? "#fff" : "#333")};
  display: flex;
  align-items: center;
  padding-left: 0.5rem;
  font-weight: 500;
`;
